import React, { useState, useEffect } from 'react';
import '../styles/stepper.css';
import gradprojects from '../data/GradProjects';
import { ExperienceWrapper, WorkWrapper, Stepper, StepperHead, LogoLink, StepperDesc } from '../styles/workStyle.js';
import Layout from '../components/layout';

const fetchPreviewImage = async (url) => {
  try {
    const response = await fetch(`https://opengraph.io/api/1.1/site/${encodeURIComponent(url)}?app_id=YOUR_APP_ID`);
    const data = await response.json();
    return data.hybridGraph.image; // Path to Open Graph image
  } catch (error) {
    console.error('Error fetching Open Graph preview:', error);
    return null; // Fallback if no preview is found
  }
};

const Projects = () => {
  const [previewImages, setPreviewImages] = useState({});

  useEffect(() => {
    gradprojects.forEach(async (item) => {
      const image = await fetchPreviewImage(item.companyLink);
      setPreviewImages((prev) => ({ ...prev, [item.id]: image }));
    });
  }, []);

  return (
    <Layout>
      <WorkWrapper style={{ marginTop: '40px' }}> {/* Add space above the entire section */}
        <h1 style={{ marginBottom: '20px' }}>Projects</h1> {/* Add space between heading and grid */}
        <div 
          className="projects-grid" 
          style={{ 
            display: 'grid', 
            gridTemplateColumns: 'repeat(2, 1fr)', 
            gap: '20px', 
            alignItems: 'flex-start' 
          }}
        >
          {gradprojects.map(item => (
            <div key={item.id} className="step" style={{ textAlign: 'center' }}>
              {/* Hyperlinked image or Open Graph preview */}
              <LogoLink href={item.companyLink} style={{ display: 'block', marginBottom: '15px' }}>
                {item.video ? (
                  <video 
                    width="100%" 
                    height="auto" 
                    style={{ objectFit: 'contain', borderRadius: '5px' }} 
                    controls 
                    loop 
                    muted 
                    autoPlay
                  >
                    <source src={item.video} type="video/mp4" />
                    Your browser does not support the video tag.
                  </video>
                ) : previewImages[item.id] ? (
                  <img 
                    src={previewImages[item.id]} 
                    alt="Preview" 
                    style={{ width: '100%', height: 'auto', objectFit: 'contain', borderRadius: '5px' }} 
                  />
                ) : (
                  <img 
                    src={item.logo} 
                    alt="Fallback Logo" 
                    style={{ width: '100%', height: 'auto', objectFit: 'contain', borderRadius: '5px' }} 
                  />
                )}
              </LogoLink>
              
              {/* Content beneath the image */}
              <div style={{ textAlign: 'left' }}> {/* Left-align the content */}
                <StepperHead>
                  <div>
                    <a>{item.name}</a>
                    <p style={{ marginBottom: '5px' }}>{item.dated}</p>
                    <div style={{ display: 'flex', justifyContent: 'start', gap: '10px', marginTop: '0px' }}>
                      <p style={{ textDecoration: 'none', color: '#6c8cd5', margin: '0' }}>{item.techUsed}</p>
                    </div>
                  </div>
                </StepperHead>
                <StepperDesc 
                  style={{ 
                    marginTop: '10px', 
                    textAlign: 'left', /* Ensure description is left-aligned */
                  }} 
                  dangerouslySetInnerHTML={{ __html: item.description }} 
                />
              </div>
            </div>
          ))}
        </div>
      </WorkWrapper>
    </Layout>
  );
};

export default Projects;
